*,
*::after,
*::before {
  padding: 0;
  margin: 0;
  box-sizing: inherit;
}

html{
  //this defines what 1rem is
  font-size: 62.5%;

  @include respond(land-tab) {
    font-size: 56.25%;
  }
  @include respond(port-tab) {
    font-size: 50%;
  }
  @include respond(big-desktop) {
    font-size: 75%;
  }
}

body {
  box-sizing: border-box;
}

