.home {
  
  &__experience{
    background-image: url("../../assets/contact_map.png");
    background-position: center;
    background-size: cover;
    margin-top: 5px;
    display: flex;
    padding-bottom: 79px;

    &__text {
      margin-top: 63px;
      margin-right: 48px;
    }

    &__button {
      margin-top: 15px;
      display: flex;
      justify-content: center;
      align-items: center;
      text-decoration: none;
      font-family: Campton;
      font-size: 16px;
      line-height: normal;
      letter-spacing: -0.004em;
      color: #FFFFFF;
      width: 220px;
      height: 58px;      
      background: #039785;
      box-shadow: 0px 4px 30px rgba(0, 0, 0, 0.12);
      border-radius: 2px;
    }
    &__cards {
      margin-top: 78px;
      display: flex;
      flex-grow:  1;
      justify-content: space-between;
    }
  }

  &__review {
    margin-top: -2px;
    background: #F2F8F7;
    padding-bottom: 50px;

    &__header {
      display: flex;
      justify-content: space-between;
      align-items: center;
      margin-bottom: 40px;
    }

    &__seemore {
      font-family: Campton;
      font-size: 12px;
      line-height: 18px;
      letter-spacing: 1px;
      text-decoration-line: underline;
      text-transform: uppercase;
      color: #002A25;
    }

    &__body {
      display: flex;
      margin-left: 5%;
      margin-bottom: 20px;
      overflow: hidden;
    }

    &__footer {
      display: flex;
      justify-content: flex-end;
    }

    &__arrowleft {
      cursor: pointer;
      width: 18px;
      height: 18px;
      display: flex;
      justify-content: center;
      align-items: center;
      border: 1px solid #039785;
      border-radius: 50%;
      transform: rotate(-180deg);
      margin-right: 10px;
    }

    &__arrowright {
      cursor: pointer;
      width: 18px;
      height: 18px;
      display: flex;
      justify-content: center;
      align-items: center;
      border: 1px solid #039785;
      border-radius: 50%;
    }
  }

  &__about {
    height: 610.53px;
    background-image: url("../../assets/contact_map.png");
    background-position: center;
    background-size: cover;
    display: flex;
    justify-content: center;
    align-items: center;

    &__button {
      text-decoration: none;
      display: flex;
      justify-content: center;
      align-items: center;
      width: 220px;
      height: 58px;
      background: #039785;
      box-shadow: 0px 4px 30px rgba(0, 0, 0, 0.12);
      border-radius: 2px;
      font-family: Campton;
      font-size: 16px;
      line-height: normal;
      letter-spacing: -0.004em;
      color: #FFFFFF;
    }

    &__video {
      position: relative;
      width: 400px;
      height: 410px;
      background: #FFFFFF;
      padding: 10px;
      box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.07);
      border-radius: 2px;
      overflow: hidden;
      overflow: hidden;

      &__control {
        width: 140px;
        height: 140px;
        background: rgba(239, 239, 239, 0.6);
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translate(-50%,-50%);
        border-radius: 50%;
        display: flex;
        justify-content: center;
        align-items: center;
      }

      &__ctrller {
        width: 80px;
        height: 80px;
        border-radius: 50%;
        display: flex;
        justify-content: center;
        align-items: center;
        background: #FFFFFF;
      }
    }

    &__info {
      padding-left: 50px;
    }


  }

  &__contact {
    height: 360px;
    top: 2355px;
    background: #F2F8F7;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    &__button {
      font-family: Campton;
      font-size: 16px;
      line-height: normal;
      letter-spacing: -0.004em;
      color: #039785;
      text-decoration: none;
      width: 220px;
      height: 58px;
      display: flex;
      justify-content: center;
      align-items: center;
      background: #FFFFFF;
      box-shadow: 0px 4px 30px rgba(0, 0, 0, 0.12);
      border-radius: 2px;
      box-shadow: 0px 4px 30px rgba(0, 0, 0, 0.12);
      border-radius: 2px;
    }
  }
}